import React, { useState, useRef } from 'react';
import './before-after.css';


const TestimonialSliderSection = () => {
  const [sliderPosition, setSliderPosition] = useState(50);
  const containerRef = useRef<HTMLDivElement>(null);

  const handleSliderChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSliderPosition(parseInt(e.target.value));
  };

  const handleContainerMove = (e: React.MouseEvent) => {
    if (!containerRef.current) return;
    
    const containerRect = containerRef.current.getBoundingClientRect();
    const relativeX = e.clientX - containerRect.left;
    const percentage = Math.max(0, Math.min(100, (relativeX / containerRect.width) * 100));
    
    setSliderPosition(percentage);
  };

  return (
    <section className="testimonial-slider-section">
      <div className="testimonial-container">
        <div className="text-content">
          <h2>Customer Success in Garden City</h2>
          
          <p>When Bill and Nancy searched for a <strong>"roofing contractor near me in Garden City"</strong>, they chose Bravo Roofing Idaho to tackle one of the most challenging homes in the neighborhood — a steep, architecturally unique property on W Beachside Ln, right in the heart of ZIP code 83714.</p>
          
          <blockquote>
            "Bravo Roofing offered a professional and timely service. They accomplished the teardown of our very steep roof, protecting both our house and vegetation. The team members that roofed the house worked in a coordinated effort. Our roof design required expert installers. We have no reservations recommending Bravo Roofing, Kyle, or his team members."
          </blockquote>
          
          <p>Their old wood shake roof had aged past its lifespan, and the slope required a skilled crew with serious experience. Our Garden City roofing team safely removed the existing materials and installed a brand-new presidential-style architectural shingle — built to last and look stunning in any season.</p>
          
          <div className="highlight-result">
            <span>★</span> The result? Another <strong>5-star roof replacement</strong> near you that's built to withstand Idaho's climate while adding curb appeal.
          </div>
          
          <p>Thank you, Bill, for trusting us with your full roofing replacement project! The new shingle roof looks amazing, and we're proud to serve neighbors like you here in Garden City 83714.</p>
          
          <p>If you live near Glenwood St, Heron Park, or along the Boise River, and are searching for a <strong>licensed roofing company near me</strong>, we're ready when you are. Call Bravo Roofing Idaho for a fast, free estimate today.</p>
          
          <button
            className="cta-button"
            onClick={() => window.location.href = '/contact'}
          >
            Get Your Free Estimate
          </button>
        </div>
        
        <div className="slider-container">
          <div 
            className="image-comparison" 
            ref={containerRef}
            onMouseMove={handleContainerMove}
          >
            <div 
              className="before-image" 
              style={{ width: `${sliderPosition}%` }}
            />
            <div className="after-image" />
            
            <input
              type="range"
              min="0"
              max="100"
              value={sliderPosition}
              onChange={handleSliderChange}
              className="slider-input"
              aria-label="Before and after comparison slider"
            />
            
            <div className="slider-handle" style={{ left: `${sliderPosition}%` }}>
              <div className="handle-circle"></div>
              
            </div>
            
            <div className="before-label">Before</div>
            <div className="after-label">After</div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default TestimonialSliderSection;